
body {
  text-align: center;
  font-family: 'Lato', sans-serif;
  font-weight: 400;
}
#timer {
  width: 100%;
  font-size: 50px;
  font-family: 'Roboto Mono', 'Courier New', monospace;
}
@media screen and (min-width: 544px) {
#timer {
    font-size: 75px;
}
}
@media screen and (min-width: 768px) {
#timer {
    font-size: 100px;
}
}
#timer span {
  padding: 0;
  margin: 0;
}
h1 {
  font-size: 30px;
  padding-top: 10px;
  font-weight: 300;
}
@media screen and (min-width: 768px) {
h1 {
    font-size: 50px;
}
}
#beer {
  display: table;
  -webkit-transform: translateX(0);
          transform: translateX(0);
  margin-left: auto;
  margin-right: auto;
  margin-top: 100px;
  margin-bottom: 60px;
}
@media screen and (min-width: 544px) {
#beer {
    margin-top: 150px;
    margin-bottom: 80px;
}
}
@media screen and (min-width: 768px) {
#beer {
    margin-top: 200px;
    margin-bottom: 100px;
}
}
.froth {
  opacity: 0;
  -webkit-transition: 1s opacity ease;
  transition: 1s opacity ease;
}
.froth.flow {
  opacity: 1;
  -webkit-transition: 1s opacity ease-in-out 0.9s;
  transition: 1s opacity ease-in-out 0.9s;
}
.beer,
.beer-b,
.bubble,
.small-bubbles,
.drip,
.liquid,
.cap {
  position: absolute;
}
.bubble,
.small-bubbles {
  border-radius: 100%;
}
.mug {
  position: relative;
  width: 20vmin;
  height: 32vmin;
  background: #e3f2fd;
  border-radius: 2vmin;
}
.mug:before {
  z-index: -2;
  left: 12vmin;
  top: 6vmin;
  width: 15vmin;
  height: 17vmin;
  border-radius: 22%;
  background: #e3f2fd;
}
.mug:after {
  z-index: -1;
  width: 12vmin;
  height: 13vmin;
  top: 8vmin;
  left: 13vmin;
  background: #fff;
  border-radius: 14%;
}
.mug:before,
.mug:after {
  position: absolute;
  content: '';
}
.beer-b {
  bottom: 2vmin;
  left: 2vmin;
  width: 16vmin;
  height: 28.5vmin;
  background: #d5ebfc;
  border-radius: 0.5vmin 0.5vmin 1vmin 1vmin;
}
.liquid {
  height: 0px;
  bottom: 0vmin;
  width: 16vmin;
  -webkit-animation: 0.3s liquidPac ease;
          animation: 0.3s liquidPac ease;
  -webkit-animation-delay: 1s;
          animation-delay: 1s;
  -webkit-transition: 1.2s height ease-in-out;
  transition: 1.2s height ease-in-out;
}
.beer {
  bottom: 2vmin;
  left: 2vmin;
  width: 16vmin;
  max-height: 29vmin;
  height: 100%;
  background: -webkit-gradient(linear, left top, left bottom, from(#ffd54f), to(#ffb300));
  background: linear-gradient(#ffd54f, #ffb300);
  -webkit-animation: 1.3s beerFill ease-in-out;
          animation: 1.3s beerFill ease-in-out;
  -webkit-animation-delay: 1s;
          animation-delay: 1s;
  opacity: 0;
}
.beer .cap {
  background-color: #fafafa;
  height: 0;
  -webkit-transition: 0.8s height ease-out;
  transition: 0.8s height ease-out;
  width: 100%;
}
.beer.filled {
  opacity: 1;
  border-radius: 0 0 1vmin 1vmin;
}
.beer.filled .cap {
  height: 1.3vmin;
  -webkit-transition: 1s height ease-out;
  transition: 1s height ease-out;
}
.beer:before,
.beer:after {
  opacity: 0;
}
.beer.carbonate:before,
.beer.carbonate:after {
  -webkit-transition: 1s opacity ease;
  transition: 1s opacity ease;
  position: absolute;
  content: '';
  border-radius: 100%;
  -webkit-animation: 2.4s carbonate infinite;
          animation: 2.4s carbonate infinite;
  -webkit-animation-delay: 1.3s;
          animation-delay: 1.3s;
}
.beer.carbonate:before {
  background: #ffc107;
  width: 2vmin;
  height: 2vmin;
  bottom: 2vmin;
  left: 5vmin;
  -webkit-box-shadow: 7vmin -15vmin 0 #ffc107, 4vmin -10vmin 0 #ffb300, 6vmin -3vmin 0 #ffc107;
          box-shadow: 7vmin -15vmin 0 #ffc107, 4vmin -10vmin 0 #ffb300, 6vmin -3vmin 0 #ffc107;
}
.beer.carbonate:after {
  background: #ffb300;
  width: 1.5vmin;
  height: 1.5vmin;
  bottom: 6vmin;
  left: 6vmin;
  -webkit-box-shadow: -3vmin -8vmin 0 #ffb300, 7vmin -5vmin 0 #ffc107;
          box-shadow: -3vmin -8vmin 0 #ffb300, 7vmin -5vmin 0 #ffc107;
}
.bubble {
  -webkit-transition: 1s opacity ease;
  transition: 1s opacity ease;
  width: 10vmin;
  height: 10vmin;
  top: -5vmin;
  left: -3vmin;
  background: #ededed;
}
.bubble:before {
  width: 12vmin;
  height: 12vmin;
  left: 5vmin;
  top: -2vmin;
}
.bubble:after {
  width: 10vmin;
  height: 10vmin;
  left: 14vmin;
  top: -2vmin;
}
.bubble:before,
.bubble:after {
  -webkit-transition: 1s opacity ease;
  transition: 1s opacity ease;
  position: absolute;
  content: '';
  background: #fafafa;
  border-radius: 100%;
}
.small-bubbles:before,
.small-bubbles:after {
  position: absolute;
  content: '';
  border-radius: 100%;
  -webkit-animation-delay: 3.8s;
          animation-delay: 3.8s;
  opacity: 0;
}
.small-bubbles {
  -webkit-transition: 1s opacity ease;
  transition: 1s opacity ease;
  background: #f5f5f5;
  width: 5vmin;
  height: 5vmin;
  top: -7vmin;
  left: 11vmin;
}
.small-bubbles:before {
  -webkit-transition: 1s opacity ease;
  transition: 1s opacity ease;
  background: #ededed;
  width: 3vmin;
  height: 3vmin;
  top: -6vmin;
  left: -1vmin;
  -webkit-animation: 5s up infinite;
          animation: 5s up infinite;
}
.small-bubbles:after {
  -webkit-transition: 1s opacity ease;
  transition: 1s opacity ease;
  background: #ededed;
  width: 3vmin;
  height: 3vmin;
  top: -8vmin;
  left: -7vmin;
  -webkit-animation: 5s up2 infinite;
          animation: 5s up2 infinite;
}
.drip {
  -webkit-transition: 1s opacity ease;
  transition: 1s opacity ease;
  width: 5vmin;
  top: 1vmin;
  border-radius: 100px;
  left: -1.7vmin;
  -webkit-box-shadow: 4vmin -4vmin 0 #fafafa;
          box-shadow: 4vmin -4vmin 0 #fafafa;
  background: #f2f2f2;
  opacity: 0;
  transition: 1s opacity ease;
}
.drip.flow {
  opacity: 1;
  -webkit-animation: 5s drip infinite;
          animation: 5s drip infinite;
  -webkit-animation-delay: 1.3s;
          animation-delay: 1.3s;
  -webkit-transition: 1s opacity ease;
  transition: 1s opacity ease;
  -webkit-transition-delay: 1.4s;
          transition-delay: 1.4s;
}
@-webkit-keyframes carbonate {
0% {
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
    opacity: 0;
}
25% {
    opacity: 1;
}
75% {
    opacity: 0;
}
100% {
    -webkit-transform: translateY(-50px);
            transform: translateY(-50px);
    opacity: 0;
}
}
@keyframes carbonate {
0% {
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
    opacity: 0;
}
25% {
    opacity: 1;
}
75% {
    opacity: 0;
}
100% {
    -webkit-transform: translateY(-50px);
            transform: translateY(-50px);
    opacity: 0;
}
}
@-webkit-keyframes up {
0% {
    -webkit-transform: translateY(10px);
            transform: translateY(10px);
    opacity: 0;
}
25% {
    opacity: 1;
}
75% {
    opacity: 0;
}
100% {
    -webkit-transform: translateY(-40px);
            transform: translateY(-40px);
    opacity: 0;
}
}
@keyframes up {
0% {
    -webkit-transform: translateY(10px);
            transform: translateY(10px);
    opacity: 0;
}
25% {
    opacity: 1;
}
75% {
    opacity: 0;
}
100% {
    -webkit-transform: translateY(-40px);
            transform: translateY(-40px);
    opacity: 0;
}
}
@-webkit-keyframes up2 {
0% {
    -webkit-transform: translateY(10px);
            transform: translateY(10px);
    opacity: 0;
}
25% {
    opacity: 1;
}
75% {
    opacity: 0;
}
100% {
    -webkit-transform: translateY(-55px);
            transform: translateY(-55px);
    opacity: 0;
}
}
@keyframes up2 {
0% {
    -webkit-transform: translateY(10px);
            transform: translateY(10px);
    opacity: 0;
}
25% {
    opacity: 1;
}
75% {
    opacity: 0;
}
100% {
    -webkit-transform: translateY(-55px);
            transform: translateY(-55px);
    opacity: 0;
}
}
@-webkit-keyframes beerFill {
0% {
    opacity: 0;
    height: 0%;
    border-radius: 0 0 0.2vmin 0.2vmin;
}
10% {
    opacity: 0.4;
}
100% {
    height: 100%;
    opacity: 1;
    border-radius: 0 0 1vmin 1vmin;
}
}
@keyframes beerFill {
0% {
    opacity: 0;
    height: 0%;
    border-radius: 0 0 0.2vmin 0.2vmin;
}
10% {
    opacity: 0.4;
}
100% {
    height: 100%;
    opacity: 1;
    border-radius: 0 0 1vmin 1vmin;
}
}
@-webkit-keyframes liquidPac {
0% {
    opacity: 0;
}
100% {
    opacity: 1;
}
}
@keyframes liquidPac {
0% {
    opacity: 0;
}
100% {
    opacity: 1;
}
}
@-webkit-keyframes drip {
0% {
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
    height: 2vmin;
    opacity: 1;
}
45% {
    opacity: 1;
}
70% {
    height: 22vmin;
    opacity: 0;
}
90% {
    height: 0;
    opacity: 0;
}
100% {
    height: 0;
    opacity: 1;
}
}
@keyframes drip {
0% {
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
    height: 2vmin;
    opacity: 1;
}
45% {
    opacity: 1;
}
70% {
    height: 22vmin;
    opacity: 0;
}
90% {
    height: 0;
    opacity: 0;
}
100% {
    height: 0;
    opacity: 1;
}
}